body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif;
}

.app {
  display: flex;
  align-items: center;
  text-align: center;
  margin: 2rem auto;
}

.app > *:first-child,
.app > *:first-child > *:first-child {
  width: 500px;
  height: 500px;
  color: red;
}
